export const api = {
  sendCode: async (email: string) => {
    try {
      const response = await axios.post("/email", {
        email,
      });
      if (response.status === 200) {
        viewToastSuccess("   .");
      }
    } catch (e) {
      if (axios.isAxiosError(e) && e.response) {
        const statusCode = e.response.status;
        if (statusCode === 409) {
          viewToastError(
            "       ."
          );
        } else if (statusCode === 500) {
          viewToastError(
            "  .    ."
          );
        } else {
          viewToastError(
            "   .    ."
          );
        }
      } else {
        viewToastError(
          "    .    ."
        );
      }
    }
  },
  verifyCode: async ({
    isPasswordValid,
    passwordConfirm,
    registerInfo,
    changeBoxContent,
  }: {
    isPasswordValid: boolean;
    passwordConfirm: string;
    registerInfo: RegisterInfo;
    changeBoxContent: () => void;
  }) => {
    const { name, studentId, major, password, verifyCode, email, userType } =
      registerInfo;

    if (userType !== "student" && userType !== "professor") {
      viewToastError("   .");
      return;
    } else if (name === "") {
      viewToastError(" .");
      return;
    } else if (studentId === 0) {
      viewToastError(" .");
      return;
    } else if (major === "") {
      viewToastError(" .");
      return;
    } else if (!password || !isPasswordValid || password !== passwordConfirm) {
      viewToastError("   .");
      return;
    }
    try {
      await axios
        .post("/verify", {
          email,
          password,
          userType,
          verifyCode,
          name,
          studentId,
          major,
        })
        .then(() => {
          viewToastSuccess(" .");
          setTimeout(() => {
            changeBoxContent();
          }, 3000);
        });
    } catch (e) {
      viewToastError("  .");
    }
  },
};
